<template>
    <div class="user_withdraw">
        <div class="form-group" style="padding: 10px 0; text-align: center;" v-if="!user.userInfo.has_draw_passwd">
                <p class="form-control-plaintext">
                    为了资金安全，请先设置
                    <span @click="pushPath('/user/modify/security_code', { query: { r: '/user/bank' } })" class="ml-1" style="text-decoration: underline;">
                    设置提款密码
                    </span>
                </p>
        </div>
        <div v-else-if="!tradeLoading">
            <div v-if="highoddsInfo" v-html="highoddsInfo" style="padding-bottom: 10px;color: #ea3535;font-size: 16px;">
            </div>

            <div v-if="hasTradeList" style="padding: 15px 15px;">
                您有待处理中的取款，请耐心等待客服审核，前往查看 <router-link to="/user/transfer" style="color: #03A9F4;">资金列表</router-link>
            </div>
        
            <div v-else>
                <!-- <div style="padding: 10px 10px 0;color: #ff9800;">
                </div> -->
                <el-form :model="formData" class="m_form" :rules="formRules" ref="form" label-width="100px" label-position="right">
                    <el-form-item 
                        class="low"
                        label="钱包余额："
                    >
                        <span class="common_money">
                            ￥{{ user.userInfo.total_money }}
                        </span>
                        <router-link to="/user/exchange" class="exchange_btn">资金回收</router-link>
                    </el-form-item>
                    <el-form-item 
                        label="提款金额："
                        prop="amount"
                    >
                    <el-input ref="amount" v-model.number="formData.amount" placeholder="请输入提款金额，最低提现100元"></el-input>
                    
                    </el-form-item>
                    <el-form-item label="提款银行卡：" class="low">
                        <div>
                            <div class="bank_select" v-if="user.bankcardList.length">
                                <span id="bank_select_wrap">{{ formData.bank ? formData.bank.open_account_bank + ', ' + formData.bank.bank_account + ', ' + formData.bank.name : '选择提现银行卡' }}</span>
                                <!-- <i class="fas fa-angle-down"></i>  -->
                            </div>
                            <span @click="pushPath('/user/bank')" v-if="!user.bankcardList.length">请先绑定银行卡</span>
                        </div>
                        <!-- <div style="line-height: 24px;" v-if="user.bankcardList.length">
                            <router-link to="/user/bank">银行卡管理</router-link>
                        </div> -->
                    </el-form-item>
                    <div class="btn_group">
                        <el-button type="primary" class="sub_btn" :loading="loading" @click="checkInfo">提交</el-button>
                    </div>
                </el-form>

            </div>

            
            <el-dialog
                title="提示"
                class="check_dialog"
                :visible.sync="checkDialogVisible"
                width="95%"
            >
                <h3>
                    <i class="el-icon-warning"></i>
                    您的流水未到达提款要求，如需提款请联系客服
                    <!-- 您的流水未到达提款要求，继续提款将会扣除 <span style="color: #F44336;">{{ (Number(formData.amount) * Number(checkList.check_fee) * 0.01).toFixed(2) }}</span> 元 -->
                    <!-- （{{ checkList.check_fee }}%） -->
                    <!-- 手续费，是否继续申请提款？（如有疑问请联系客服） -->
                </h3>
                    
                <el-table
                    :data="checkList.items"
                    style="width: 100%"
                    class="m_table"
                    size="small"
                >
                    <el-table-column
                        prop="created_at"
                        label="时间">
                        <template slot-scope="scope">
                            {{ formatTime(scope.row.created_at) }}
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="type"
                        label="类型" width="50">
                        <template slot-scope="scope">
                            {{ checkType[scope.row.type] }}
                        </template>
                    </el-table-column>
    
                    <el-table-column
                        prop="recharge_price"
                        label="金额">
                        <template slot-scope="scope">
                            ￥{{ scope.row.recharge_price }}
                        </template>
                    </el-table-column>

                    <el-table-column
                        prop="recharge_price"
                        label="存款稽核点">
                        <template slot-scope="scope">
                            {{ scope.row.stream_price }} / {{ scope.row.recharge_price * scope.row.check }}
                        </template>
                    </el-table-column>
                    
                    <el-table-column
                        prop="is_ok"
                        label="状态">
                        <template slot-scope="scope">
                            <el-tag :type="scope.row.is_ok == 'Y' ? 'success' : 'danger'">
                                {{ scope.row.is_ok == 'Y' ? '满足' : '未满足' }}
                            </el-tag>
                        </template>
                    </el-table-column>
                </el-table>

                <span slot="footer" class="dialog-footer">
                    <el-button @click="checkDialogVisible = false">取 消</el-button>
                    <!-- <el-button type="primary" :loading="loading" @click="showCheckDrawPassword">继续申请提款</el-button> -->
                </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import MobileSelect from 'mobile-select'
import { withdraw, checkInfo, checkDrawpassword, judegehighodds } from '@/api/user'
import { formatTime } from '@/utils/index'
import { trade } from '@/api/user'

const minWithdraw = 100;

export default {
    name: 'User-Withdraw',
    props: ['type', 'app', 'user'],
    components: {

    },

    data() {
        return {
            formData: {
                bank: null,
                amount: '',
            },
            formRules: {
                amount: [
                    { required: true, message: '金额不能为空', trigger: 'blur'},
                    { type: 'number', message: '金额必须为数字', trigger: 'blur'},
                    { type: 'number', min: 100, message: '最低提现'+ minWithdraw +'元', trigger: 'blur' },
                    { type: 'number', max: Number(this.user.userInfo.total_money), message: '不能超过您的钱包余额', trigger: 'blur' },
                ]
            },
            loading: false,
            checkDrawLoading: false,
            checkList: {},
            checkDialogVisible: false,
            highoddsInfo: '',
            checkType: {
                'recharge': '充值',
                'stream': '返水',
                'activity': '活动',
                'highodds': '高赔率活动稽核',
            },
            tradeLoading: true,
            hasTradeList: false,
        }
    },

    computed: {
        
    },

    mounted() {
        this.$refs.amount && this.$refs.amount.$el.focus()
        this.init();
    },

    methods: {
        init() {
            if(!this.user.bankcardList.length) {
                this.$store.dispatch('bankcardList').then(() => {
                    if(this.user.bankcardList.length) {
                        this.$nextTick(() => {
                            this.initBankSelect();
                        });
                    }
                });
            }else {
                this.formData.bank = this.user.bankcardList[0];
                this.initBankSelect();
            }

            //  judegehighodds().then(json => {
            //     // console.log(json);
            //     if(json.code == 200) {
            //         const list = json.list;

            //         let productText;
            //         if(list.productIds.length) {
            //             productText = this.product.list.filter(item => {
            //                 return list.productIds.indexOf(item.id) > -1;
            //             }).map(item => {
            //                 const link = this.$router.resolve('/p?id=' + item.id).href;
            //                 return `<a href="${link}" target="_blank">${item.name}</a>`;
            //             }).join(', ');
            //         }else {
            //             productText = '其他游戏投注';
            //         }

            //         this.highoddsInfo = `<span>因您玩过高倍彩票，需要到 ${ productText }, 玩够 ${ list.recharge_price } 流水（已经有流水: ${ list.stream_price }），否则提现需要收取一定比例手续费</span>`;
            //     }
            // })

            this.checkTrade();
        },

        // 检查是否有进行中的提款
        checkTrade() {
            const data = {
                type: 2,
                page: 1,
                count: 1,
                status: 0,
            }

            this.tradeLoading = true;
            trade(data).then(json => {
                this.tradeLoading = false;
				if (json.code == 200) {
                   this.hasTradeList = !!json.list.data.length;
				}
			});
        },

        initBankSelect() {
            const data = [];
            this.user.bankcardList.forEach(bank => {
                data.push({
                    id: bank.bank_account,
                    value: bank.open_account_bank + ', ' + bank.bank_account + ', ' + bank.name
                });
            });
            
            this.formData.bank = this.user.bankcardList[0];
            // this.$nextTick(() => {
            //     new MobileSelect({
            //         trigger: '#bank_select_wrap',
            //         title: '选择银行卡',
            //         wheels: [
            //             {
            //                 data: data
            //             }
            //         ],
            //         position:[0],
            //         callback: (indexArr, data) => {
            //             const index = indexArr[0];
            //             this.formData.bank = this.user.bankcardList[index];
            //         }
            //     })
            // });
        },

        showCheckDrawPassword() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    if(this.checkDrawLoading) return ;

                    this.$prompt('请输入6位提款密码', '提示', {
                        confirmButtonText: '确定',
                        inputType: 'password',
                        cancelButtonText: '取消',
                        inputPattern: /[0-9]{6}/,
                        inputErrorMessage: '提款密码格式不正确',
                        customClass: 'draw_password_message_box',
                        beforeClose: (action, instance, done) => {
                            if (action === 'confirm') {
                                instance.confirmButtonLoading = true;
                                instance.confirmButtonText = '提交中...';
                                this.checkDrawLoading = true;
                                const value = instance.$refs.input.value;
                                this.submitForm(value, () => {
                                    done();
                                }, () => {
                                    instance.confirmButtonLoading = false;
                                    instance.confirmButtonText = '提交';
                                });
                            } else {
                                done();
                            }
                        }
                    })
                    .then(({ value }) => {
                        // this.checkDrawLoading = true;
                        // this.submitForm(value);
                        // checkDrawpassword(value).then(json => {
                        //     if(json.code == 200) {
                        //         this.submitForm();
                        //     }else {
                        //         this.$message.error('提款码错误');
                        //     }
                        //     this.checkDrawLoading = false;
                        // })
                    }).catch(() => {
                            
                    });
                }
            });
        },

        checkInfo() {
            this.$refs.form.validate((valid) => {
                if(valid) {
                    this.loading = true;
                    checkInfo().then(json => {
                        this.loading = false;
                        if (json.code == 200) {
                            if(json.list.items.length > 0) {
                                this.checkList = json.list;
                                this.checkDialogVisible = true;    
                            }else {
                                this.showCheckDrawPassword();
                            }
                        }
                        else {
                            this.$message.error(json.info);
                        }

                    });
                }
            });
        },

        submitForm(draw_passwd, success, error) {
            if(this.loading) return ;

            this.$refs.form.validate((valid) => {
                if (valid) {
                    const data = {
                        bankcard_id: this.formData.bank.id,
                        amount: this.formData.amount,
                        draw_passwd,
                    }

                    if(data.amount > this.user.userInfo.total_money) {
                        this.$message.error('不能超过您的总额');
                        return ;
                    }

                    this.loading = true;
                    withdraw(data).then(json => {
                        this.loading = false;
                        if (json.code == 200) {
                            success && success();
                            this.$message.success('订单提交成功，请等待客服审核');
                            this.pushPath('/user/transfer');
                        } else {
                            error && error();
                            this.$message.error(json.info);
                        }
                    });
                }
            });
        },

        formatTime(t) {
            return formatTime(t);
        },

        pushPath(path, params) {
            // if(this.app.isApp) {
            //     this.$store.dispatch('postMsgToApp', {
            //         action: 'presentModal',
            //         data: {
            //             url: path,
            //             params
            //         }
            //     })
            //     return ;
            // }
            params = params || {};
            params.path = path
            this.$router.push(params);
        }
    }
}
</script>

