    methods: {
        init() {
        },

        getPokerClass(p) {
            p = p.split('_');

            const t = {
                '0': 'heart',
                '1': 'spade',
                '2': 'diamond',
                '3': 'club',
            }
            return 'gr-' + t[p[0]] + '-' + (p[1] ? p[1].toLocaleLowerCase() : '');
        },

    }
}
