<script>
import { mapGetters } from 'vuex'
import Transfer from './components/Transfer'
import Bank from './components/Bank'
import Security from './components/Security'
import Modify from './components/Modify'
import Withdraw from './components/Withdraw'
import Exchange from './components/Exchange'
import Order from './components/Order'
import PaymentRedirect from './components/PaymentRedirect'
import { moneyToFixed } from '@/filters/index'

export default {
    name: 'User-Index',
    components: {
			Transfer,
			Bank,
			Security,
			Modify,
			Withdraw,
			Exchange,
			Order,
			PaymentRedirect,
    },

    data() {
        return {
			vipInfo: null,
			list: [
				{
					path: '/user/order',
					name: '投注记录',
					icon: 'fa fa-list-alt',
					class: 'order',
				},
				{
					path: '/user/transfer',
					name: '资金记录',
					icon: 'fas fa-yen-sign',
					class: 'transfer',
				},
				{
					path: '/user/bank',
					name: '银行卡',
					icon: 'fa fa-credit-card',
					class: 'bank',
				},
				{
					path: '/user/security',
					name: '安全中心',
					icon: 'fas fa-user-shield',
					class: 'security',
				},
				{
					path: '/help',
					name: '联系客服',
					icon: 'fas fa-comment-dots',
					class: 'help',
				},
			],
        }
	},

	computed: {
		app() { return this.$store.getters.app },
		user() { return this.$store.getters.user },
		product() { return this.$store.getters.product },
		page() { return this.$route.params.page || '' },
		type() { return this.$route.params.type },
		isLogin() {
			return this.user.roles.indexOf('user') > -1;
		},
	},

    mounted() {
		this.init()
    },

    methods: {
      	init() {
			if(this.page) {
				if(!this.isLogin) {
					this.$router.push({ path: '/auth/login' });
					return ;
				}
			}

			this.$store.dispatch('getConfig', 'vip').then(list => {
                this.vipInfo = list;
            });
		},
		
		logout() {
				this.$store.dispatch('logout').then(() => {
				//    this.$router.push({ path: this.redirect || '/' })
				})
		},
		
		getTotalMoney() {
				let money = Number(this.user.userInfo.total_money);
				
				for(const key in this.product.balance) {
						if(Number(key) !== 0) {
							money += Number(this.product.balance[key].money);
						}
				}

				return money.toFixed(2);
		},

		pushPath(path) {
			if(this.app.isApp) {
				if(['/auth/login', '/auth/register'].indexOf(path) > -1) {
					this.$store.dispatch('postMsgToApp', {
							action: 'presentModal',
							data: {
								url: path
							}
					})
					return ;
				}
			}

			this.$router.push({ path: path });
		},

        getPercentage(a, b) {
            if(b == 0) {
                return 100;
            }

            const p = Number(a)/Number(b);

            if(p > 1) {
                return 100;
            }

            return moneyToFixed(p * 100);
        },
	}
}
</script>

