<template>
    <div class="user_transfer">

        <div class="filter">
            <div>
                <label>类型：</label>
                <div id="type_select">
                    <el-button class="select_btn" size="mini">
                        {{ formData.selectType ? formData.selectType.value : '请选择' }}
                        <i class="el-icon-arrow-down"></i>
                    </el-button>
                </div>
            </div>
            <div>
                <label>日期：</label>
                <span>
                    <span @click="calendarIsShow = true">
                        <el-button class="select_btn" size="mini">
                            {{ formData.startDate ? formData.startDate.format('YYYY-MM-DD') : '开始日期' }}
                            <i class="el-icon-date"></i>
                        </el-button>
                    </span>
                     至
                    <span @click="calendarIsShow = true">
                        <el-button class="select_btn" size="mini">
                            {{ formData.endDate ? formData.endDate.format('YYYY-MM-DD') : '结束日期' }}
                            <i class="el-icon-date"></i>
                        </el-button>
                    </span>
                </span>
            </div>
            <div>
                <el-button type="primary" size="mini" @click="filterSubmit">筛选</el-button>
                &nbsp;
                <a href="javascript:;" @click="clearFilter">清除</a>
            </div>
        </div>

        <div 
        v-loading="loading" 
        element-loading-text="加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        style="padding: 0 10px;">
            <el-table
                ref="table"
                :data="tradeList.data"
                @row-click="tableRowClick"
                class="m_table"
                size="small"
            >
                <el-table-column 
                    type="expand"
                    width="20"
                >
                    <template slot-scope="scope">
                        <el-form label-position="right" label-width="75px" size="small" class="table-expand">

                            <el-form-item label="订单号">
                                <span>{{ scope.row.trade_no }}</span>
                            </el-form-item>

                            <el-form-item label="类型">
                                <span>{{ scope.row.type_text }}</span>
                            </el-form-item>

                            <el-form-item label="金额">
                                <span :class="{color_green: isAmountInput(scope.row), color_red: !isAmountInput(scope.row) }">
                                    <span v-if="scope.row.commission == 0">{{ (isAmountInput(scope.row) ? '+' : '-') + '￥' + scope.row.amount }}</span>
                                    <span v-if="scope.row.commission != 0">{{ (isAmountInput(scope.row) ? '+' : '-') + '￥' + (Number(scope.row.amount)).toFixed(2) }}(手续费：{{ scope.row.commission }})</span>
                                </span>
                            </el-form-item>

                            <el-form-item label="交易相关">
                                <span>{{ getAcoutCodeText(scope.row) }}{{ scope.row.desc ? '，' + scope.row.desc : '' }}</span>
                            </el-form-item>

                            <el-form-item label="银行卡号" v-if="scope.row.acount && scope.row.acount.acount">
                                <span>{{ scope.row.acount.acount }}</span>
                            </el-form-item>

                            <el-form-item label="姓名" v-if="scope.row.acount && scope.row.acount.name">
                                <span>{{ scope.row.acount.name }}</span>
                            </el-form-item>

                            <el-form-item label="状态">
                                <el-tag size="small" effect="dark" :type="getType(scope.row.status)">{{ scope.row.status_text }}</el-tag>
                            </el-form-item>

                            <el-form-item label="创建日期">
                                <span v-html="parseTime(scope.row.created_at, '{m}-{d} {h}:{i}:{s}')"></span>
                            </el-form-item>

                            <el-form-item label="修改日期">
                                <span v-html="parseTime(scope.row.modify_at, '{m}-{d} {h}:{i}:{s}')"></span>
                            </el-form-item>

                        </el-form>
                    </template>
                </el-table-column>

                <el-table-column
                    prop="amount"
                    label="金额"
                >
                    <template slot-scope="scope">
                        <span :class="{color_green: isAmountInput(scope.row), color_red: !isAmountInput(scope.row) }">
                            <span>{{ (isAmountInput(scope.row) ? '+' : '-') + '￥' + (Number(scope.row.amount)).toFixed(2) }}</span>
                        </span>
                    </template>
                </el-table-column>

                <!-- <el-table-column
                    prop="current_total"
                    label="账户余额"
                >
                    <template slot-scope="scope"> 
                        {{ scope.row.status == 2 ? '￥' + scope.row.current_total : '' }}
                    </template>
                </el-table-column> -->

                <el-table-column
                    prop="type_text"
                    label="类型"
                ></el-table-column>
                <el-table-column
                    prop="status_text"
                    label="状态"
                    width="60"
                >
                    <template slot-scope="scope"> 
                        <!-- <span :class="{color_progress: scope.row.status == 0, color_green: scope.row.status == 2, color_red: scope.row.status == 3 }">
                            {{ scope.row.status_text }}
                        </span> -->
                        <el-tag size="small" :type="getType(scope.row.status)" effect="dark">{{ scope.row.status_text }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="modify_at"
                    label="修改日期"
                >
                    <template slot-scope="scope">
                        <span v-html="parseTime(scope.row.modify_at, '{m}-{d} {h}:{i}')"></span>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                background
                :pager-count="5"
                layout="prev, pager, next"
                @current-change="pageChange"
                :page-count="tradeList.total_pages"
            >
            </el-pagination>
        </div>
        <calendar ref="calendar" mode="during" :show.sync="calendarIsShow" @change="onCalendarChange" />
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import { pickerOptions, formatTime, parseTime, formatBankAcount } from '@/utils/index'
import { trade } from '@/api/user'
import MobileSelect from 'mobile-select'

export default {
    name: 'User-Transfer',
    props: ['type', 'app', 'user', 'product'],
    components: {
        
    },

    data() {
        return {
            tradeList: {},
            datePickerOptions: {
                shortcuts: pickerOptions
            },
            loading: false,
            calendarIsShow: false,
            formData: {
                selectType: '',
                page: 1,
                startDate: '',
                endDate: '',
            },
            typeOptions: [
                {
                    id: '0',
                    value: '所有类型'
                },
                {
                    id: '1',
                    value: '充值'
                },
                {
                    id: '2',
                    value: '提现'
                },
                {
                    id: '3',
                    value: '钱包转入'
                },
                {
                    id: '4',
                    value: '钱包转出'
                },
                {
                    id: '5',
                    value: '系统上分'
                },
                {
                    id: '6',
                    value: '系统返水发放'
                },
                {
                    id: '7',
                    value: '代理返水发放'
                },
                {
                    id: '8',
                    value: '系统减分'
                },
                {
                    id: '9',
                    value: '活动赠送'
                },
                { id: '18', value: '代理转入' },
            ]
        }
    },

    computed: {
        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && checkNavIsShow(item.id);
            });

            return list;
        }
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            this.requestTrade();

            new MobileSelect({
                trigger: "#type_select",
                title: "类型",
                wheels: [
                    {
                        data: this.typeOptions
                    }
                ],
                triggerDisplayData: false,
                callback: (indexArr, data) => {
                    this.formData.selectType = data[0];
                }
            });


            // 同步一次余额
            const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
            this.$store.dispatch('productBalance', { product_ids })
        },

        tableRowClick(row) {
            this.$refs.table.toggleRowExpansion(row);
        },

        requestTrade() {
            if(this.loading) return ;
            const data = {
                type: this.formData.selectType.id,
                page: this.formData.page,
            }

            if(this.formData.startDate) {
                data.start_at = this.formData.startDate.hour(0).minute(0).second(0).unix();
            }

            if(this.formData.endDate) {
                data.end_at = this.formData.endDate.hour(23).minute(59).second(59).unix();

                if(data.start_at && Number(data.end_at) < Number(data.start_at)) {
                    this.$message.error('结束日期不能小于开始日期');
                    return ;
                }
            }

            this.loading = true;
            trade(data).then(json => {
                this.loading = false;
				if (json.code == 200) {
                    this.tradeList = json.list;
                    
				}else if(json.code == 10002) {
                    this.tradeList = {};
                }
			});
        },

        formatAmount(n) {
            const i = n.indexOf('.00');
            if(i>-1) {
                return n.slice(0, i);
            }

            return n;
        },

        isAmountInput(row) {
            const type = Number(row.type);

            if([1, 3, 5, 6, 7, 9, 18].indexOf(type) > -1) {
                return true;
            }

            return false;
        },

        getType(status) {
            let type;
            if(status == 0 || status == 1) {
                   type = 'warning';
            }else if(status == 2) {
                   type = 'success';
            }else if(status == 3 || status == 4) {
                   type = 'danger';
            }else if(status == 5 || status == 6) {
                   type = 'info';
            }

            return type;
        },

        getAcoutCodeText(row) {
            if(row.type == 1) {
                return row.acount.type_text + '充值';
            }else if(row.type == 2) {
                return '提现至银行卡号：' + (row.acount.acount);
            }else if(row.type == 3) {
                return row.product.name + ' > 钱包';
            }else if(row.type == 4) {
                return  '钱包 > ' + row.product.name;
            }else if(row.type == 5) {
                return  '系统上分' + (row.sub_type_text ? ('-' + row.sub_type_text) : '');
            }else if(row.type == 6) {
                return  '系统返水发放';
            }else if(row.type == 7) {
                return  '代理返水发放';
            }else if(row.type == 18) {
                return '代理转入';
            }

            return '';
        },

        filterSubmit() {
            this.requestTrade();
        },
        pageChange(currentPage) {
            this.formData.page = currentPage;
            this.requestTrade();
        },

        parseTime(time, format) {
            return parseTime(time, format);
        },

        onCalendarChange(date) {
            this.formData.startDate = date[0];
            if(date[1]) {
                this.formData.endDate = date[1];
                this.calendarIsShow = false;
            }
        },

        clearFilter() {
            this.formData.selectProduct = '';
            this.formData.startDate = '';
            this.formData.endDate = '';

            this.$refs.calendar.changeDate([null, null]);
        }

    }
}
</script>

