<template>
    <div class="user_exchange">

        <el-table
            :data="[totalMoney, userMoney].concat(filterProduct)"
            class="table_wrap"
            :show-header="false"
            v-loading="pageLoading"
            element-loading-text="加载中"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
        >

            <el-table-column
                label="名称"
                width="105"
            >
                <template slot-scope="scope">
                    <div :class="scope.row.is_fix == 'Y' ? 'fixed' : ''">
                        <img v-if="scope.row.id > 0 && scope.row.icon" :src="scope.row.icon | imgFormatter" height="12" />
                        {{ scope.row.name }}
                    </div>
                </template>
            </el-table-column>
            
            <el-table-column
                fixed="right"
                label="操作"
            >
                <template slot-scope="scope">
                    <div v-if="(scope.row.id == 0 || scope.row.id == -1) || scope.row.is_fix == 'N'">
                        <div class="handle_row" v-if="scope.row.id > 0">
                            <a href="javascript:;" v-if="scope.row.is_fix == 'N'" class="product_reload reload" @click="productReload(scope.row.id)">
                                <i class="el-icon-refresh"></i>
                            </a>
                            <span class="money">￥{{ getMoney(scope.row.id) }}</span>
                            <el-button type="text" @click="openForm(scope.row.id, 2)">转入</el-button>
                            <el-button type="text" :loading="btnLoading[scope.row.id]" @click="openForm(scope.row.id, 3)">全部回收</el-button>
                        </div>
                        <div class="handle_row" v-if="scope.row.id == 0">
                            <span class="money">￥{{ getMoney(scope.row.id) }}</span>
                            <el-button type="text" :loading="btnLoading[scope.row.id]" @click="openForm(scope.row.id, 3)">一键全部回收</el-button>
                        </div>
                        <div class="handle_row" v-if="scope.row.id == -1">
                            <span class="money">￥{{ getTotalMoney() }}</span>
                            <!-- <a href="javascript:;" class="reload" @click="reload">
                                <i class="el-icon-refresh"></i>
                            </a> -->
                        </div>
                    </div>
                    <div v-if="scope.row.is_fix == 'Y'">
                        <div class="handle_row" :class="scope.row.is_fix == 'Y' ? 'fixed' : ''" style="padding-right: 10px;">
                            <span class="money">￥{{ getMoney(scope.row.id) }}</span>
                            接口维护中
                        </div>
                    </div>
                </template>
            </el-table-column>
            
        </el-table>


        <el-dialog
            title="提示"
            :visible.sync="dialogVisible"
            width="90%"
        >
            <p><span v-html="fromText"></span><i class="fas fa-long-arrow-alt-right" style="margin: 0 1rem;"></i> <span v-html="toText"></span></p>
            <el-form ref="form" class="" :model="formData" :rules="formRules" label-position="top" label-width="80px">
                <span v-if="getMoney(formData.type == 2 ? 0 : formData.product_id) == 0">余额不足，无法进行操作</span>
                    
                <el-form-item 
                    v-if="getMoney(formData.type == 2 ? 0 : formData.product_id) > 0"
                    :label="'转' + (formData.type == 2 ? '入' : '出') + '金额'"
                    prop="amount"
                >
                    <el-input type="number" placeholder="请输入金额" v-model="formData.amount"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button v-if="getMoney(formData.type == 2 ? 0 : formData.product_id) > 0" type="primary" :loading="submitLoading" @click="submitForm">确定</el-button>
            </span>
        </el-dialog>
    </div>

    
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import { transfer } from '@/api/user'

const exchangeReloadTime = 2000;

export default {
    name: 'User-Exchange',
    props: ['type', 'user', 'product'],
    components: {
        
    },

    data() {
        var validateAmount = (rule, value, callback) => {
            if(value == '') {
                callback(new Error('金额不能为空'));
            }
            else if (isNaN(parseFloat(value))) {
                callback(new Error('金额必须为数字'));
            }else if (parseFloat(value) < 1) {
                callback(new Error('最少转入1元'));
            } else {
                callback();
            }
        }
        
        return {
            totalMoney: {
                id: -1,
                name: '总资产',
            },
            userMoney: {
                id: 0,
                name: '账户余额',
                icon: '',
            },
            pageLoading: false,
            submitLoading: false,
            btnLoading: {

            },
            dialogVisible: false,
            fromText: '',
            toText: '',

            formData: {
                type: 0,
                amount: '',
                product_id: 0,
            },

            formRules: {
                amount: [
                    { require: true, validator: validateAmount, trigger: 'blur'},
                ]
            }
        }
    },

    computed: {
        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && checkNavIsShow(item.id);
            });

            return list;
        }
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            this.reload();
        },

        reload() {
            if(this.pageLoading) return ;

            this.pageLoading = true;
            const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
            this.$store.dispatch('productBalance', { product_ids }).then(() => {
                setTimeout(() => {
                    this.pageLoading = false;
                }, 500);
            });
        },


        productReload(product_id) {
            if(this.pageLoading) return ;

            this.pageLoading = true;
            this.$store.dispatch('productBalanceSync', { product_ids: product_id }).then((json) => {
                this.pageLoading = false;
                if(json.code == 200) {
                    this.$message.success('操作成功，同步余额已受理，请稍后查看金额');
                    setTimeout(() => {
                        this.reload();
                    }, 1500);
                }else {
                    this.$message.error(json.info);
                }
            });
        },

        getMoney(id) {
            let money = 0;

            if(id == 0) {
                money = this.user.userInfo.total_money;
            }else {
                for(const key in this.product.balance) {
                    if(key == id) {
                        money = this.product.balance[key].money;
                    }
                }
            }
            
            return Number(money).toFixed(2);
        },

        getTotalMoney() {
            let money = Number(this.user.userInfo.total_money);
            
            for(const key in this.product.balance) {
                if(Number(key) !== 0) {
                    money += Number(this.product.balance[key].money);
                }
            }

            return money.toFixed(2);
        },

        submitForm() {
            if(this.submitLoading) return ;

            this.$refs.form.validate((valid) => {
                if (valid) {
                    const data = Object.assign({}, this.formData);

                    data.amount = Number(data.amount);

                    const compareMoney = data.type == 2 ? this.getMoney(0) : this.getMoney(data.product_id);
                    if(data.amount > compareMoney) {
                        this.$message.error('余额不足');
                        return ;
                    }

                    this.submitLoading = true;
                    transfer(data).then(json => {
                        this.submitLoading = false;
                        if (json.code == 200) {
                            this.$message({
                                message: '操作成功，转账已受理，请稍后查看金额',
                                type: 'success',
                                customClass: 'long',
                                duration: 3000,
                                center: true,
                            });

                            setTimeout(() => {
                                this.reload();

                                setTimeout(() => {
                                    this.reload();
                                }, exchangeReloadTime)
                            }, exchangeReloadTime);
                            
                            this.dialogVisible = false;
                        } else {
                            this.$message.error(json.info);
                        }
                    });
                }
            });
        },

        openForm(id, type) {
            this.formData.type = type;
            this.formData.product_id = id;
            this.formData.amount = '';

            if(type == 3) {
                if(this.btnLoading[id]) return ;

                const data = {
                    type,
                    product_id: id ? id : this.filterProduct.map(item => { return item.id }).join(','),
                }

                this.$set(this.btnLoading, id, true);
                transfer(data).then(json => {
                    this.$set(this.btnLoading, id, false);
                    if (json.code == 200) {
                        this.$message({
                            message: '操作成功，转账已受理，请稍后查看金额',
                            type: 'success',
                            customClass: 'long',
                            duration: 3000,
                            center: true,
                        });

                        setTimeout(() => {
                            this.reload();

                            setTimeout(() => {
                                this.reload();
                            }, exchangeReloadTime)
                        }, exchangeReloadTime);
                    } else {
                        this.$message.error(json.info);
                    }
                });
                return ;
            }

            let product = this.filterProduct.filter(p => p.id == id );

            if(!product.length) return ;

            product = product[0];

            const productMoneyText = product.name + ' <span class="money">￥'+ this.getMoney(id) +'</span>';
            const moneyText = '钱包余额 <span class="money">￥'+ this.getMoney(0) +'</span>';

            if(type == 2) {
                this.toText = productMoneyText;
                this.fromText = moneyText;
            }else {
                this.fromText = productMoneyText;
                this.toText = moneyText;
            }
            
            this.dialogVisible = true;
        }
    }
}
</script>

<style lang="scss">

.user_exchange {

    .table_wrap {
        background: transparent;
        color: #fff;


        .el-button {
            padding: 6px 12px;
        }

        .handle_row {
            text-align: right;
            
            .money {
                margin-right: 10px;
            }
        }

        th, tr {
            background: transparent;
        }

        tr.hover-row>td {
            background: #525252;
        }

        td, th.is-leaf {
            border-bottom-color: #8a8a8a;
        }

        .fixed {
            filter: grayscale(100%);
            color: #ccc;
        }
    }

    .el-button+.el-button {
        margin-left: 0;
    }

    .money {
        color: #f88311;
    }

    .reload {
        font-size: 18px;
        color: #409EFF;
    }

    .product_reload {
        margin-right: 10px;
    }


   
	
}
</style>
