<template>
    <div class="user_modify">
        <el-form ref="form" class="m_form" :model="formData" :rules="formRules" :label-width="labelWidth" label-position="right">
            <div v-if="type == 'password'">
                <el-form-item prop="oldPassword" label="旧密码：">
                    <el-input type="password" v-model="formData.oldPassword" placeholder="请输入旧密码"></el-input>
                </el-form-item>
                <el-form-item prop="password" label="新密码：">
                    <el-input type="password" v-model="formData.password" placeholder="请输入新密码"></el-input>
                </el-form-item>
                <el-form-item prop="rePassword" label="确认新密码：">
                    <el-input type="password" v-model="formData.rePassword" placeholder="请再次输入新密码"></el-input>
                </el-form-item>
            </div>

            <div v-if="type == 'security_code'">
                <el-form-item prop="oldSecurityCode" label="旧提款密码：" v-if="user.userInfo.has_draw_passwd">
                    <el-input type="password" v-model="formData.oldSecurityCode" placeholder="请输入旧6位提款密码"></el-input>
                </el-form-item>
                <el-form-item prop="securityCode" v-bind:label="(!user.userInfo.has_draw_passwd?'':'新') + '提款密码：'">
                    <el-input type="password" v-model="formData.securityCode" v-bind:placeholder="'请输入' + (!user.userInfo.has_draw_passwd? '' : '新') + '6位提款密码'"></el-input>
                </el-form-item>
                <el-form-item prop="reSecurityCode"  v-bind:label="'确认' + (!user.userInfo.has_draw_passwd?'':'新') + '提款密码：'">
                    <el-input type="password" v-model="formData.reSecurityCode" v-bind:placeholder="'请再次输入' + (!user.userInfo.has_draw_passwd? '' : '新') + '6位提款密码'"></el-input>
                </el-form-item>

            </div>

            <div v-if="type == 'find_security_code'">
                <div class="find_text" v-if="!hasPhoneOrEmail">
                    <p class="form-control-plaintext">
                        绑定邮箱或手机号后才能找回提款密码。 
                    </p>
                    <el-button type="primary">
                        <span @click="pushPath('/user/modify/phone')">绑定手机号</span>&nbsp;
                    </el-button>
                    <el-button type="primary">
                        <span @click="pushPath('/user/modify/email')">绑定邮箱</span>
                    </el-button>
                    <el-button @click="back">取消</el-button>
                </div>

                <el-tabs style="padding: 0 15px;" value="phone" v-if="hasPhoneOrEmail">
                    <el-tab-pane label="通过手机号找回" name="phone" v-if="user.userInfo.mobile_phone">
                        <el-form-item prop="phoneCode"  label="输入手机验证码">
                            <el-input v-model.number="formData.phoneCode" placeholder="请输入手机验证码"></el-input>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="通过邮箱找回" name="email" v-if="user.userInfo.email">
                        <el-form-item prop="emailCode" label="输入邮箱验证码">
                            <el-input v-model.number="formData.emailCode" placeholder="请输入邮箱验证码"></el-input>
                        </el-form-item>
                    </el-tab-pane>
                </el-tabs>
            </div>

            <div v-if="type == 'phone'">
                <el-form-item prop="phone" :label="(hasPhone ? '新' : '绑定') + '手机号：'">
                    <el-input v-model="formData.phone" v-bind:placeholder="'请输入' + (hasPhone ? '新' : '') + '手机号'"></el-input>
                </el-form-item>
                <!-- <el-form-item prop="phoneCode" label="验证码：">
                    <el-input v-model.number="formData.phoneCode" placeholder="请输入验证码"></el-input>
                </el-form-item> -->
            </div>

            <div v-if="type == 'email'">
                <el-form-item prop="email" v-bind:label="(hasEmail ? '新' : '绑定') + '邮箱：'">
                    <el-input v-model="formData.email" v-bind:placeholder="'请输入' + (hasEmail ? '新' : '') + '邮箱'"></el-input>
                </el-form-item>
                <!-- <el-form-item prop="emailCode" label="验证码：">
                    <el-input v-model.number="formData.emailCode" placeholder="请输入验证码"></el-input>
                </el-form-item> -->
            </div>

            <div class="btn_group" v-if="showSubmitBtn">
                <el-row :gutter="10">
                    <el-col :span="12">
                        <div class="grid-content bg-purple">
                            <el-button type="primary" :loading="loading" @click="modify">提交</el-button>
                        </div>
                    </el-col>
                    <el-col :span="12">
                        <div class="grid-content bg-purple">
                            <el-button @click="back()">取消</el-button>
                        </div>
                    </el-col>
                </el-row>
            </div>
        </el-form>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import { 
    passwordValidator, 
    drawPasswordValidator, 
    phoneValidator, 
    emailValidator,
    phoneCodeValidator,
    emailCodeValidator,
} from '@/utils/validator'
import { modify } from '@/api/user'

export default {
    name: 'User-Modify',
    props: ['type', 'app', 'user'],
    components: {
        
    },

    data() {
        return {
            formData: {
                oldPassword: '',
                password: '',
                rePassword: '',

                oldSecurityCode: '',
                securityCode: '',
                reSecurityCode: '',

                phone: '',
                phoneCode: '',

                email: '',
                emailCode: '',
            },

            formRules: {
                oldPassword: passwordValidator,
                password: passwordValidator,
                rePassword: passwordValidator,

                oldSecurityCode: drawPasswordValidator,
                securityCode: drawPasswordValidator,
                reSecurityCode: drawPasswordValidator,

                phone: phoneValidator,
                phoneCode: phoneCodeValidator,
                email: emailValidator,
                emailCode: emailCodeValidator,
            },
            
            loading: false
        }
    },

    computed: {

        hasPhoneOrEmail() {
            return this.user.userInfo.mobile_phone || this.user.userInfo.email;
        },
        hasPhone() {
            return this.user.userInfo.mobile_phone;
        },
        hasEmail() {
            return this.user.userInfo.email;
        },
        showSubmitBtn() {
            if(this.type == 'find_security_code' && !this.hasPhoneOrEmail) {
                return false;
            }

            return true;
        },

        labelWidth() {
            let w = 110;

            if(this.type=='security_code' || this.type=='find_security_code') {
                w = 140;
            }

            return w  + 'px';
        }
    },

    mounted() {
        
    },

    methods: {
        modify() {
            const data = {}, formData = this.formData, type = this.type;
            var r = decodeURIComponent(this.$route.query.r || '');

            this.$refs.form.validate((valid) => {
                if (valid) {
                    switch(type) {
                        case 'security_code':
                            if (formData.securityCode != formData.reSecurityCode) {
                                this.$message.error('提款码不一致');
                                return;
                            }

                            if(this.user.userInfo.has_draw_passwd) {

                                if(formData.oldSecurityCode == formData.securityCode) {
                                    this.$message.error('不能与原提款码一致');
                                    return;
                                }

                                data.old_draw_passwd = formData.oldSecurityCode;
                                data.draw_passwd = formData.securityCode;
                            }else {
                                data.key = 'draw_passwd';
                                data.val = formData.securityCode;
                            }
                        break;
                        case 'password':
                            if (formData.password != formData.rePassword) {
                                this.$message.error('密码不一致');
                                return ;
                            }

                            if(formData.oldPassword == formData.password) {
                                this.$message.error('不能与原密码一致');
                                return;
                            }

                            data.old_password = formData.oldPassword;
				            data.password = formData.password;
                        break;
                        case 'phone':
                            data.key = 'mobile_phone';
                            data.val = formData.phone;
                        break;
                        case 'email':
                            data.key = 'email';
                            data.val = formData.email;
                        break;
                    }

                    data.type = type;

                    if(this.loading) return ;
                    this.loading = true;
                    modify(data).then(json => {
                        this.loading = false;
                        if (json.code == 200) {
                            this.$message.success('提交成功');
                            this.$store.dispatch('getUserInfo');

                            if(this.app.isApp) {
                                this.$store.dispatch('postMsgToApp', {
                                    action: 'goBack-reload',
                                })
                                return ;
                            }

                            this.$router.push({ path: r || '/user/security' });
                        } else {
                            this.$message.error(json.info);
                        }
                    });
                }
            });
        },
        
        back(r) {
            if(this.app.isApp) {
                this.$store.dispatch('postMsgToApp', {
                    action: 'goBack',
                })
                return ;
            }

            this.$router.push({ path: r || '/user/security' });
        },

        pushPath(path, params) {
            // if(this.app.isApp) {
            //     this.$store.dispatch('postMsgToApp', {
            //         action: 'presentModal',
            //         data: {
            //             url: path,
            //             params
            //         }
            //     })
            //     return ;
            // }

            this.$router.push({ path: path }, params);
        }
    }
}
</script>

<style scoped lang="scss">

.user_modify {
    padding: 10px;
	.find_text {
        padding: 15px;

        a {
            color: #fff;
            text-decoration: none;
        }
    }
}

</style>
