<template>
    <div class="user_order">

        <div class="filter">
            <div>
                <label>游戏类型：</label>
                <div id="type_select">
                    <el-button class="select_btn" size="mini">
                        {{ formData.selectProduct ? formData.selectProduct.value : '选择游戏' }}
                        <i class="el-icon-arrow-down"></i>
                    </el-button>
                </div>
            </div>
            <div>
                <label>日期：</label>
                <span>
                    <span @click="calendarIsShow = true">
                        <el-button class="select_btn" size="mini">
                            {{ formData.startDate ? formData.startDate.format('YYYY-MM-DD') : '开始日期' }}
                            <i class="el-icon-date"></i>
                        </el-button>
                    </span>
                     至
                    <span @click="calendarIsShow = true">
                        <el-button class="select_btn" size="mini">
                            {{ formData.endDate ? formData.endDate.format('YYYY-MM-DD') : '结束日期' }}
                            <i class="el-icon-date"></i>
                        </el-button>
                    </span>
                </span>
            </div>
            <div>
                <el-button type="primary" size="mini" @click="filterSubmit">筛选</el-button>
                &nbsp;
                <a href="javascript:;" @click="clearFilter">清除</a>
            </div>
        </div>

        <div 
            v-loading="loading" 
            element-loading-text="加载中"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
        >
            <el-table
            ref="table"
            :data="list.items"
            @row-click="tableRowClick"
            @expand-change="expandChange"
             class="m_table"
             size="mini"
             style="width: 100%"
            >
                <el-table-column type="expand" width="20">
                    <template slot-scope="scope">
                        <div v-if="detailLoading[scope.row.id]">
                            <div style="text-align: center;padding: 20px 0;">
                            <i class="el-icon-loading"></i> 详情加载中
                            </div>
                        </div>
                        <GameDetail v-if="!detailLoading[scope.row.id] && scope.row.detail" :data="scope.row" :detail="scope.row.detail" :statusText="statusOptions[scope.row.status]" />
                    </template>
                </el-table-column>
                <el-table-column
                    label="游戏"
                >
                    <template slot-scope="scope">
                       <div>{{ scope.row.product_name }}</div>
                       <div>{{ scope.row.game_name }}</div>
                    </template>
                </el-table-column>
                <el-table-column
                    label="有效投注"
                >
                    <template slot-scope="scope">
                        {{ '￥' + scope.row.vaild_cathectic_total_price }}
                    </template>
                </el-table-column>

                <el-table-column
                    label="损益"
                >
                    <template slot-scope="scope">
                        {{ scope.row.status == 3 ? ('￥' + scope.row.balance_total_price) : '-' }}
                    </template>
                </el-table-column>

                <el-table-column
                    label="状态"
                >
                    <template slot-scope="scope">
                        <el-tag size="small" effect="dark" :type="statusClassOptions[scope.row.status]">{{ statusOptions[scope.row.status] }}</el-tag>
                    </template>
                </el-table-column>

                <el-table-column
                    label="时间"
                    width="60"
                >
                    <template slot-scope="scope">
                        <div v-html="formatTime(scope.row.begintime)">
                        </div>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                background
                layout="prev, pager, next"
                @current-change="pageChange"
                :pager-count="5"
                :page-count="list.total_pages"
            >
            </el-pagination>
        </div>
        <div class="total_data" :style="{ bottom: (app.isApp ? 0 : 50) + 'px' }" v-if="totalData">
            <el-row>
                <el-col :span="12">
                    <div class="grid-content bg-purple">
                        投注额：￥{{ totalData.cathectic_total_price }}
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="grid-content bg-purple">
                        有效投注额：￥{{ totalData.vaild_cathectic_total_price }}
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="grid-content bg-purple">
                        派彩：￥{{ totalData.winning_total_price }}
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="grid-content bg-purple">
                        笔数：{{ totalData.total_order }}
                    </div>
                </el-col>
            </el-row>
        </div>
        <calendar ref="calendar" mode="during" :show.sync="calendarIsShow" @change="onCalendarChange" />
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import { pickerOptions, formatTime, formatBankAcount } from '@/utils/index'
import { getList, getDetail } from '@/api/order'
import GameDetail from './GameDetail'
import MobileSelect from 'mobile-select'

export default {
    name: 'User-Order',
    props: ['type', 'user', 'product', 'app'],
    components: {
        GameDetail,
    },

    data() {
        return {
            list: {},
            totalData: null,
            datePickerOptions: {
                shortcuts: pickerOptions
            },
            loading: false,
            detailLoading: {},
            formData: {
                selectProduct: '',
                page: 1,
                startDate: '',
                endDate: '',
            },
            calendarIsShow: false,

            statusOptions: {
                1: '已创建',
                2: '已支付',
                3: '已完成',
                4: '取消',
            },
            statusClassOptions: {
                '1': 'warning',
                '2': 'info',
                '3': 'success',
                '4': 'info',
            },
        }
    },

    computed: {
        productListFilter() {
            let list = [];

            this.product.navList.forEach(item => {
                if(item.is_show == 'Y' && item.list && item.list.length) {
                    list = list.concat( item.list.filter(l => l.is_show == 'Y') );
                }
            })

            return list;
        },
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            this.requestList();

            new MobileSelect({
                trigger: "#type_select",
                title: "游戏类型",
                wheels: [
                    {
                        data: this.productListFilter.map(item => {
                            return {
                                id: item.id,
                                value: item.name,
                            };
                        })
                    }
                ],
                triggerDisplayData: false,
                callback: (indexArr, data) => {
                    this.formData.selectProduct = data[0];
                }
            });
        },

        requestList() {
            if(this.loading) return ;
            const data = {
                product_id: this.formData.selectProduct.id,
                page: this.formData.page,
            }

            if(this.formData.startDate) {
                data.starttime = this.formData.startDate.hour(0).minute(0).second(0).unix();
            }

            if(this.formData.endDate) {
                data.endtime = this.formData.endDate.hour(23).minute(59).second(59).unix();

                if(data.starttime && Number(data.endtime) < Number(data.starttime)) {
                    this.$message.error('结束日期不能小于开始日期');
                    return ;
                }
            }

            this.loading = true;
            getList(data).then(json => {
                this.loading = false;
                if (json.code == 200) {
                    this.list = json.list;
                    if(json.list.totalData) {
                        this.totalData = json.list.totalData;
                    }
                    
				}else if(json.code == 10002) {
                    this.list = {};
                }
            });
        },

        formatTime(t) {
            return formatTime(t).replace(' ', '<br/>');
        },

        isAmountInput(row) {
            if(row.type == 1) {
                return true;
            }else if(row.type == 2) {
                return false;
            }else if(row.type == 3) {
                return true;
            }else if(row.type == 4) {
                return false;
            }

            return true;
        },

        getTradeAt(row) {
            return formatTime(row.trade_at == '0' ? row.created_at : row.trade_at);
        },

        getAcoutCodeText(row) {
            if(row.type == 1) {
                return row.acount.type_text + '充值';
            }else if(row.type == 2) {
                return '提现至银行卡号：' + formatBankAcount(row.acount.acount);
            }else if(row.type == 3) {
                return row.product.name + ' 转至 余额';
            }else if(row.type == 4) {
                return  '余额 转至 ' + row.product.name;
            }

            return '';
        },

        filterSubmit() {
            this.requestList();
        },

        pageChange(currentPage) {
            this.formData.page = currentPage;
            this.requestList();
        },

        tableRowClick(row) {
            this.$refs.table.toggleRowExpansion(row);
        },

        expandChange(row, expandedRows) {
            if(!row.detail) {
                if(this.detailLoading[row.id]) return ;
                this.$set(this.detailLoading, row.id, true);
                getDetail(row.order_sn).then(json => {
                    this.$set(this.detailLoading, row.id, false);
                    if (json.code == 200) {
                        row.detail = json.list;
                    }
                });
            }
        },

        getPokerClass(p) {
            p = p.split('_');

            const t = {
                '0': 'heart',
                '1': 'spade',
                '2': 'diamond',
                '3': 'club',
            }
            
            return 'gr-' + t[p[0]] + '-' + p[1].toLocaleLowerCase();
        },

        onCalendarChange(date) {
            this.formData.startDate = date[0];
            if(date[1]) {
                this.formData.endDate = date[1];
                this.calendarIsShow = false;
            }
        },

        clearFilter() {
            this.formData.selectProduct = '';
            this.formData.startDate = '';
            this.formData.endDate = '';

            this.$refs.calendar.changeDate([null, null]);
        }
    }
}
</script>

