<template>
    <div class="user_bank">
        <div class="form-group" v-if="!user.userInfo.has_draw_passwd">
                <p class="form-control-plaintext">
                    为了资金安全，请先设置
                    <span @click="pushPath('/user/modify/security_code', { query: { r: '/user/bank' } })" class="ml-1" style="text-decoration: underline;">
                    设置提款密码
                    </span>
                </p>
        </div>
        <div v-if="user.userInfo.has_draw_passwd" v-loading="loading">
            <div v-if="user.bankcardList.length" style="padding: 15px 15px 0; color: #cc5151; font-size: 12px;">如需修改或删除银行卡请联系客服</div>

            <div class="card_wrap" v-if="!addBankCardShow">
                <div class="bank_section bank_item rounded" :key="card.id" v-for="card in user.bankcardList">
                    <div>{{ card.open_account_bank }}（{{ card.name }}）</div>
                    <div class="number">{{ card.bank_account }}</div>
                    
                    <!-- <el-dropdown trigger="click">
                        <span class="el-dropdown-link">
                            <i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item>
                                <div @click="bankEdit(card)">编辑</div>
                            </el-dropdown-item>
                            <el-dropdown-item>
                                <div @click="bankDel(card)">删除</div>
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown> -->
                </div>
            </div>
            
            <div v-if="!addBankCardShow && !user.bankcardList.length" class="card_wrap">
                <a href="javascript:;" @click="bankCreate" class="add_new bank_section rounded">
                    绑定银行卡
                </a>
            </div>

            <div class="new_card" v-if="addBankCardShow">
                <el-form :model="bankForm" :rules="formRules" ref="bankForm" label-width="80px" label-position="right" class="m_form">
                    <el-form-item prop="open_account_bank" label="开户银行">
                        <div id="open_account_bank_select">{{ bankForm.open_account_bank ? bankForm.open_account_bank : '请选择' }}</div>
                    </el-form-item>
                    <el-form-item prop="areaSelect" label="开户城市">
                        <div id="area_select">{{ bankForm.areaSelect.length == 2 ? bankForm.areaSelect[0] + '-' + bankForm.areaSelect[1] : '请选择' }}</div>
                    </el-form-item>
                    <el-form-item prop="bank_account" label="银行卡号">
                        <el-input v-model="bankForm.bank_account" placeholder="请输入银行卡号"></el-input>
                    </el-form-item>

                    <el-form-item prop="name" label="开户姓名">
                        <el-input v-model="bankForm.name" placeholder="请输入姓名"></el-input>
                    </el-form-item>

                    <div class="btn_group">
                        <el-row :gutter="10">
                            <el-col :span="12">
                                <div class="grid-content bg-purple">
                                    <el-button type="primary" @click="addBankCardSubmit">提交</el-button>
                                </div>
                            </el-col>
                            <el-col :span="12">
                                <div class="grid-content bg-purple">
                                    <el-button @click="addBankCardShow = false">取消</el-button>
                                </div>
                            </el-col>
                        </el-row>
                    </div>

                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import MobileSelect from 'mobile-select'
import { addBankcard, delBankcard } from '@/api/user'
import { getAddr } from '@/api/common'
import { 
    bankAccountValidator,
    bankNameValidator,
} from '@/utils/validator'

export default {
    name: 'User-Bank',
    props: ['type', 'app', 'user'],
    components: {

    },

    data() {

        var validateArea = (rule, value, callback) => {
            if (!value || value.length != 2) {
                callback(new Error('请选择开户城市'));
            } else {
                callback();
            }
        };

        return {
            dialogType: 'create',
            bank_list: [
                '工商银行',
                '建设银行',
                '农业银行',
                '民生银行',
                '招商银行',
                '交通银行',
                '中国银行',
                '邮政储蓄',
                '中信银行',
                '兴业银行',
                '华夏银行',
                '浦发银行',
                '广发银行',
                '平安银行',
                '光大银行',
                '农商银行', 
                '台州银行'
            ],

            addBankCardShow: false,

            bankForm: {
                id: '',
                open_account_bank: '',
                areaSelect: [],
                bank_account: '',
                name: '',
            },

            formRules: {
                open_account_bank: [{ required: true, message: '请选择开户银行', trigger: 'change' }],
                areaSelect: [{ required: true, validator: validateArea, trigger: 'change' }],
                bank_account: bankAccountValidator,
                name: bankNameValidator,
            },
            
            areaOptions: [],

            loading: false,

        }
    },

    computed: {

    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            if(!this.user.bankcardList.length) {
                this.getBankcardList();
            }
        },

        getBankcardList() {
            if(this.loading) return;
            this.loading = true;
            return this.$store.dispatch('bankcardList').then(() => {
                this.loading = false;
            })
        },

        addBankCardSubmit() {
            this.$refs.bankForm.validate((valid) => {
                if (valid) {

                    var data = {
                        id: this.bankForm.id,
                        name: this.bankForm.name,
                        open_account_bank: this.bankForm.open_account_bank,
                        bank_account: this.bankForm.bank_account,
                        open_account_province: this.bankForm.areaSelect[0] + '-' + this.bankForm.areaSelect[1],
                        open_account_address: '',
                        id_card: '',
                    }

                    if(this.loading) return ;
                    this.loading = true;

                    addBankcard(data).then(json => {
                        this.loading = false;

                        if (json.code == 200) {
                            this.$message.success('提交成功');
                            this.getBankcardList();
                            this.addBankCardShow = false;
                        } else {
                            this.$message.error(json.info);
                        }
                    });
                }
            })
        },

        bankCreate() {
            this.bankForm = {
                id: '',
                open_account_bank: '',
                areaSelect: [],
                bank_account: '',
                name: '',
            }

            this.dialogType = 'create';


            this.addBankCardShow = true;
            
            this.$nextTick(this.initArea)

        },

        bankEdit(card) {
            this.$alert('如需修改或删除银行卡请联系客服', '', {
                confirmButtonText: '确定',
                callback: action => {
                    
                }
            });
            return ;

            this.bankForm.id = card.id;
            this.bankForm.open_account_bank = card.open_account_bank;
            this.bankForm.areaSelect = card.open_account_province.split('-');
            this.bankForm.bank_account = card.bank_account;
            this.bankForm.name = card.name;
            this.dialogType = 'update';


            this.addBankCardShow = true;

            this.$nextTick(this.initArea)
        },

		bankDel(card) {
            this.$alert('如需修改或删除银行卡请联系客服', '', {
                confirmButtonText: '确定',
                callback: action => {
                    
                }
            });
            return ;
            
            if(!confirm('确认删除吗')) return ;
            
			delBankcard(card.id).then(json => {
				if (json.code == 200) {
                    this.$message.success('删除成功');
                    this.getBankcardList();
				} else {
					this.$message.error(json.info);
				}
			});
        },

        initArea() {
            if(this.areaOptions.length == 0) {
                getAddr().then(json => {
                    if (json.code == 200) {
                        const areaOptions = json.list;
                        
                        areaOptions.forEach(province => {
                            province.id = province.value = province.label = province.name;
                            province.childs = province.cities;

                            province.childs.forEach(city => {
                                city.id = city.value = city.label = city.name;
                            });
                        });

                        this.areaOptions = areaOptions;

                        this.$nextTick(() => {
                            this.initSelect();
                        })

					} else {
						this.$message.error(json.info);
					}
                })
            }else {
                this.$nextTick(() => {
                    this.initSelect();
                })
            }
        },
        
        initSelect() {
            // 开户行
            let bankPos = this.bank_list.findIndex((name) => { return name == this.bankForm.open_account_bank });
            bankPos = bankPos == -1 ? 0 : bankPos;

            new MobileSelect({
                trigger: '#open_account_bank_select',
                title: '选择开户行',
                wheels: [
                    {
                        data: this.bank_list.map(name => {
                            return {
                                id: name,
                                value: name,
                            }
                        })
                    }
                ],
                position:[bankPos],
                callback: (indexArr, data) => {
                    const index = indexArr[0];
                    this.bankForm.open_account_bank = this.bank_list[index];
                }
            });

            // 选择开户城市

            let areaPos = [0];

            if(this.bankForm.areaSelect.length == 2) {
                const pos1 = this.areaOptions.findIndex(item => item.name == this.bankForm.areaSelect[0]);
                const pos2 = this.areaOptions[pos1].childs.findIndex(item => item.name == this.bankForm.areaSelect[1]);
                areaPos = [pos1, pos2];
            }

            new MobileSelect({
                trigger: '#area_select',
                title: '选择开户城市',
                wheels: [
                    {
                        data: this.areaOptions
                    }
                ],
                position: areaPos,
                callback: (indexArr, data) => {
                    this.bankForm.areaSelect = [
                        this.areaOptions[indexArr[0]].name,
                        this.areaOptions[indexArr[0]].childs[indexArr[1]].name,
                    ];
                    // console.log(this.bankForm.areaSelect)
                }
            }); 

        },

        pushPath(path, params) {
            // if(this.app.isApp) {
            //     this.$store.dispatch('postMsgToApp', {
            //         action: 'presentModal',
            //         data: {
            //             url: path,
            //             params
            //         }
            //     })
            //     return ;
            // }

            this.$router.push({ path: path }, params);
        }
    }
}
</script>

